@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500);
.event-details {
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
  text-align: center; }

.event-details img {
  height: 60px;
  margin: 10px; }

.event-details h2 {
  font-size: 16px;
  margin-bottom: 0px;
  line-height: 1.5; }

.event-details p {
  font-size: 12px; }

.primary {
  color: #007a7c;
  font-weight: 800; }

.lead {
  font-weight: 800px;
  color: #007a7c; }

.container {
  margin-top: 50px;
  margin-bottom: 50px; }

.row {
  justify-content: center; }

.footer {
  background: #e5e5e5;
  padding: 50px; }

.icon {
  margin: 5px;
  color: #545556; }

#event-detail {
  width: 250px; }

